import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Select from "react-select";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const EditServiceLayer = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const centreId = localStorage.getItem("centreId");

  const [service, setService] = useState({
    category: "",
    selectedService: "",
    actualPrice: "",
    discountedPrice: "",
    platformFees: 99,
    note: "",
    shiftTimes: [{ day: "", from: "", to: "" }],
    doctor: [],
    reportingTime: "",
    description: "",
    totalPrice: "",
  });

  const [loading, setLoading] = useState(false);
  const [status, setStatus] = useState("");
  const [categories, setCategories] = useState([]);
  const [categoryService, setCategoryService] = useState({
    category: "",
    services: [],
  });
  const [doctors, setDoctors] = useState([]);

  // Get the selected category object
  const selectedCategory = categories.find(
    (cat) => cat.category_name === service.category
  );
  // console.log("seleceted catgeory",selectedCategory);

  const selectedCategoryId = selectedCategory?._id;
  console.log("Id", selectedCategoryId);

  const platformFees =
    selectedCategory?.category_name === "OPERATIVE PROCEDURES" ? 999 : 99;

  // // Get the selected service object
  // const selectedService = categoryService.services[0]?.services?.find(
  //   (srv) => srv._id === service.selectedService
  // );

  // Fetch existing service data
  useEffect(() => {
    const fetchServiceDetails = async () => {
      try {
        setLoading(true);
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centreService/single/${id}`
        );

        const serviceData = res.data;

        // Determine correct platform fees based on category
        const correctPlatformFees =
          serviceData.service.serviceCategory === "OPERATIVE PROCEDURES"
            ? 999
            : 99;

        setService({
          category: serviceData.service.serviceCategory,
          selectedService: serviceData.service.serviceName,
          actualPrice: serviceData.service.actualPrice,
          discountedPrice: serviceData.service.discountPrice,
          platformFees: correctPlatformFees,
          note: serviceData.service.note || "",
          shiftTimes:
            serviceData.service.slots && serviceData.service.slots.length > 0
              ? serviceData.service.slots.map((slot) => ({
                  ...slot,
                  from: convertTo24Hour(slot.from),
                  to: convertTo24Hour(slot.to),
                }))
              : [{ day: "", from: "", to: "" }],
          doctor: serviceData.service.doctors
            ? serviceData.service.doctors.map((d) => d._id)
            : [],
          reportingTime: serviceData.service.reportTiming || "",
          description: serviceData.service.description || "",
          totalPrice: serviceData.service.totalPrice || "",
        });

        // Fetch services for this category
        // if (serviceData.service.serviceCategory) {
        //   await fetchCategoryServices(serviceData.service.serviceCategory);
        // }
      } catch (error) {
        console.error("Error fetching service details:", error);
        toast.error("Failed to load service details");
      } finally {
        setLoading(false);
      }
    };

    if (id) {
      fetchServiceDetails();
    }
  }, [id]);

  // Fetch all categories
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/category/getAllCategories`
        );
        setCategories(res.data);
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    };
    fetchCategories();
  }, []);

  // Fetch services for a specific category
  // Define this before useEffect
  const fetchCategoryServices = async (categoryId) => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/${categoryId}`
      );

      const servicesArray = Array.isArray(response.data)
        ? response.data
        : response.data
        ? [response.data]
        : [];

      setCategoryService({
        category: categoryId,
        services: servicesArray,
      });
    } catch (err) {
      console.error("Error fetching services:", err);
    }
  };

  // Then inside useEffect:
  useEffect(() => {
    if (selectedCategoryId) {
      fetchCategoryServices(selectedCategoryId);
    }
  }, [selectedCategoryId]);

  // Recalculate platform fees and total price when category changes
  useEffect(() => {
    if (service.category) {
      const correctPlatformFees =
        service.category === "OPERATIVE PROCEDURES" ? 999 : 99;

      setService((prev) => {
        // Only update if platform fees are different
        if (prev.platformFees !== correctPlatformFees) {
          const updated = { ...prev, platformFees: correctPlatformFees };

          // Recalculate total price
          const actual = Number(updated.actualPrice) || 0;
          const discount = Number(updated.discountedPrice) || 0;

          if (discount > 0) {
            updated.totalPrice = discount + correctPlatformFees;
          } else if (actual > 0) {
            updated.totalPrice = actual + correctPlatformFees;
          }

          return updated;
        }
        return prev;
      });
    }
  }, [service.category]);

  // Fetch doctors for the centre
  useEffect(() => {
    const fetchDoctors = async () => {
      if (!centreId) {
        toast.error("Centre ID not found in localStorage");
        return;
      }
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/doctors/centre/${centreId}`
        );
        setDoctors(res.data);
      } catch (error) {
        console.error("Error fetching doctors:", error);
      }
    };
    if (centreId) {
      fetchDoctors();
    }
  }, [centreId]);

  // Fetch centre status
  useEffect(() => {
    const fetchCentreDetails = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centres/${centreId}`
        );
        setStatus(res.data.status);
      } catch (error) {
        console.error("Error fetching centre details", error.message);
      }
    };
    if (centreId) {
      fetchCentreDetails();
    }
  }, [centreId]);

  // Doctor select options
  const doctorOptions = [
    { value: "add_doctor", label: "Add New Doctor" },
    ...doctors.map((doc) => ({
      value: doc._id,
      label: doc.name,
    })),
  ];

  const selectedDoctorOptions = doctorOptions.filter((opt) =>
    service.doctor.includes(opt.value)
  );

  const handleDoctorChange = (selected) => {
    if (!selected) {
      setService((prev) => ({ ...prev, doctor: [] }));
      return;
    }

    const selectedValues = selected.map((opt) => opt.value);
    if (selectedValues.includes("add_doctor")) {
      navigate("/create-doctor");
      return;
    }

    setService((prev) => ({
      ...prev,
      doctor: selected.map((opt) => opt.value),
    }));
  };

  const handleCategoryChange = async (e) => {
    const selectedCategoryId = e.target.value;

    setService({
      ...service,
      category: selectedCategoryId,
      selectedService: "",
    });
    console.log("Category id", selectedCategoryId);

    await fetchCategoryServices(selectedCategoryId);
  };

  const handleServiceChange = (e) => {
    const selectedServiceId = e.target.value;
    setService({
      ...service,
      selectedService: selectedServiceId,
    });
  };

  const handleChange = (e) => {
    const { name, value, type, selectedOptions, multiple } = e.target;
    console.log("Name", name);

    if (multiple && selectedOptions) {
      const values = Array.from(selectedOptions, (option) => option.value);
      setService((prev) => ({
        ...prev,
        [name]: values,
      }));
    } else {
      const newValue =
        type === "number" ? (value === "" ? "" : Number(value)) : value;

      setService((prev) => {
        const updated = { ...prev, [name]: newValue };

        // âœ… Adjust platform fee based on category
        if (
          (name === "category" && newValue === "OPERATIVE PROCEDURES") ||
          prev.category === "OPERATIVE PROCEDURES"
        ) {
          updated.platformFees = 999;
        } else if (name === "category") {
          updated.platformFees = 99;
        }

        const actual =
          name === "actualPrice"
            ? Number(newValue)
            : Number(updated.actualPrice);
        const discount =
          name === "discountedPrice"
            ? Number(newValue)
            : Number(updated.discountedPrice);
        const fees = Number(updated.platformFees) || 0;

        // âœ… New Logic
        if (!isNaN(discount) && discount > 0) {
          const total = discount + fees;

          if (actual && actual <= total) {
            alert(
              `Actual price can't be less than or equal to total price (â‚¹${total})`
            );
            updated.discountedPrice = "";
            updated.totalPrice = actual + fees; // revert to actual if invalid
          } else {
            updated.totalPrice = total;
          }
        } else if (!isNaN(actual) && actual > 0) {
          // discount is empty or removed â†’ use actual
          updated.totalPrice = actual + fees;
        } else {
          updated.totalPrice = "";
        }

        return updated;
      });
    }
  };

  const handleShiftTimeChange = (index, field, value) => {
    const updated = [...service.shiftTimes];
    updated[index][field] = value;
    setService((prev) => ({ ...prev, shiftTimes: updated }));
  };

  const addShiftTime = () => {
    setService((prev) => ({
      ...prev,
      shiftTimes: [...prev.shiftTimes, { day: "", from: "", to: "" }],
    }));
  };

  const removeShiftTime = (index) => {
    const updated = service.shiftTimes.filter((_, i) => i !== index);
    setService((prev) => ({ ...prev, shiftTimes: updated }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (status !== "Approved") {
      toast.error(
        "Please Complete your profile details and wait for admin approval to start listing your services."
      );
      return;
    }

    // Open confirm modal instead of directly saving
    setConfirmModal(true);
  };

  const [confirmModal, setConfirmModal] = useState(false);

  const modalRef = useRef(null);

  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        confirmModal
      ) {
        confirmModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // ðŸ”‘ Scroll lock logic
    if (confirmModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto"; // Cleanup when component unmounts
    };
  }, [confirmModal]);

  const confirmSaveService = async (e) => {
    setConfirmModal(false);
    try {
      setLoading(true);
      const payload = {
        serviceCategory: service.category, // BLOOD BANK
        serviceName: service.selectedService, // CRYOPRECIPITATE ON REPLACEMENT
        actualPrice: service.actualPrice,
        discountPrice: service.discountedPrice,
        platformFees: service.platformFees,
        note: service.note,
        reportTiming: service.reportingTime,
        description: service.description,
        totalPrice: service.totalPrice,
        centre: centreId, // from props/context
        doctors: service.doctor, // array of doctor IDs
        slots: service.shiftTimes.map((slot) => ({
          day: slot.day,
          from: convertTo12Hour(slot.from),
          to: convertTo12Hour(slot.to),
        })),
      };
      await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/centreService/${id}`,
        payload
      );
      toast.success("Service updated successfully!");
      setTimeout(() => {
        navigate("/follower");
      }, 3000);
    } catch (error) {
      console.error("Error updating service:", error);
      toast.error(error.response?.data?.message || "Failed to update service");
    } finally {
      setLoading(false);
    }
  };

  const convertTo24Hour = (time12h) => {
    if (!time12h) return "";

    // If already 24h format
    if (/^\d{2}:\d{2}$/.test(time12h)) {
      return time12h;
    }

    const parts = time12h.split(" ");
    if (parts.length !== 2) {
      return time12h; // fallback
    }

    let [time, modifier] = parts;
    let [hours, minutes] = time.split(":");

    if (hours === "12") hours = "00";
    if (modifier.toLowerCase() === "pm") {
      hours = String(parseInt(hours, 10) + 12);
    }

    return `${hours.padStart(2, "0")}:${minutes}`;
  };

  const convertTo12Hour = (time) => {
    if (!time) return "";

    const [hour, minute] = time.split(":");
    const date = new Date();
    date.setHours(hour, minute, 0);

    return date.toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
      hour12: true,
    });
  };

  if (loading) {
    return <div className="text-center py-5">Loading service details...</div>;
  }
  return (
    <div className="dashboard-body__content">
      <div className="row gy-4">
        <div className="col-12">
          <div className="card common-card border border-gray-five">
            <div className="card-body">
              <h3 className="mb-4">Edit Service</h3>
              {status !== "Approved" && (
                <p className="text-danger">
                  Please wait for admin approval to update your services.
                </p>
              )}
              <form encType="multipart/form-data" onSubmit={handleSubmit}>
                <div className="row g-3">
                  {/* Category */}
                  <div className="col-md-6">
                    <label className="form-label">
                      Service Category <span className="text-danger">*</span>
                    </label>
                    <select
                      // disabled
                      name="category"
                      value={service.category}
                      onChange={handleCategoryChange}
                      className="form-select"
                      required
                    >
                      <option value="" disabled>
                        Select a Category
                      </option>
                      {categories.map((cat) => (
                        <option key={cat.id} value={cat.category_name}>
                          {cat.category_name}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-md-6">
                    <label className="form-label">
                      Service <span className="text-danger">*</span>
                    </label>
                    <select
                      name="selectedService"
                      value={service?.selectedService}
                      onChange={handleChange}
                      className="form-select"
                      required
                      // disabled
                    >
                      <option value="" disabled>
                        Select Service
                      </option>
                      {Array.isArray(categoryService.services[0]?.services) &&
                        categoryService.services[0].services.map((service) => (
                          <option key={service._id} value={service.serviceName}>
                            {service.serviceName}
                          </option>
                        ))}
                      <option value={service?.selectedService}>
                        {service?.selectedService}
                      </option>
                    </select>
                  </div>
                  {/* Actual Price */}
                  <div className="col-md-4">
                    <label className="form-label">
                      {selectedCategory?.category_name ===
                      "OPERATIVE PROCEDURES"
                        ? "Examination Fees (â‚¹)"
                        : "Actual Price (â‚¹)"}
                    </label>
                    <input
                      type="number"
                      name="actualPrice"
                      value={service.actualPrice}
                      onChange={handleChange}
                      className="form-control"
                      min="1"
                      placeholder="e.g. 500"
                      required
                    />
                  </div>

                  {/* Discounted Price */}
                  <div className="col-md-4">
                    <label className="form-label">
                      {selectedCategory?.category_name ===
                      "OPERATIVE PROCEDURES"
                        ? "Discounted Fees (â‚¹)"
                        : "Discounted Price (â‚¹)"}
                    </label>
                    <input
                      type="number"
                      name="discountedPrice"
                      value={service.discountedPrice}
                      onChange={handleChange}
                      className="form-control"
                      min="1"
                      placeholder="e.g. 300"
                    />
                  </div>

                  <div className="col-md-4">
                    <label className="form-label">
                      {selectedCategory?.category_name ===
                      "OPERATIVE PROCEDURES"
                        ? "Total Fees (â‚¹) Included â‚¹999 Platform Fees"
                        : "Total Price (â‚¹)"}
                      {/* <span className="text-danger">*</span> */}
                    </label>
                    <input
                      type="number"
                      name="totalPrice"
                      value={service.totalPrice}
                      onChange={handleChange}
                      className="form-control"
                      placeholder="e.g. 800"
                      readOnly
                      required
                    />
                  </div>

                  {selectedCategory?.category_name ===
                  "OPERATIVE PROCEDURES" ? (
                    <div className="col-md-6">
                      <label className="form-label">
                        Note <span className="text-danger">*</span>
                      </label>
                      <input
                        type="text"
                        name="note"
                        value={service.note}
                        onChange={handleChange}
                        className="form-control"
                        placeholder="Write your note"
                        required
                      />
                    </div>
                  ) : (
                    <div className="col-md-6">
                      <p className="text-muted">
                        <span>Note: </span> â‚¹99 platform fee will be added and
                        is included in the total price visible to the patient.
                      </p>
                    </div>
                  )}

                  {/* Doctor */}
                  <div className="col-md-6">
                    <label className="form-label">Doctor</label>
                    <Select
                      isMulti
                      name="doctor"
                      value={selectedDoctorOptions}
                      onChange={handleDoctorChange}
                      options={doctorOptions}
                      placeholder="Select Doctor(s)..."
                      classNamePrefix="react-select"
                      menuPortalTarget={document.body}
                      styles={{
                        menuPortal: (base) => ({
                          ...base,
                          zIndex: 9999,
                        }),
                      }}
                      required
                    />
                  </div>

                  {/* Shift Time */}
                  <div className="col-md-12">
                    <label className="form-label">Time Slots</label>

                    {service.shiftTimes.map((slot, index) => (
                      <div
                        key={index}
                        className="row align-items-center mb-2 shift-row"
                      >
                        <div className="col-md-3">
                          <select
                            name="day"
                            className="form-control"
                            value={slot.day}
                            onChange={(e) =>
                              handleShiftTimeChange(
                                index,
                                "day",
                                e.target.value
                              )
                            }
                            required
                          >
                            <option value="" disabled>
                              Select a Day
                            </option>
                            <option value="Sunday">Sunday</option>
                            <option value="Monday">Monday</option>
                            <option value="Tuesday">Tuesday</option>
                            <option value="Wednesday">Wednesday</option>
                            <option value="Thursday">Thursday</option>
                            <option value="Friday">Friday</option>
                            <option value="Saturday">Saturday</option>
                          </select>
                        </div>
                        <div className="col-md-3">
                          <input
                            type="time"
                            value={slot.from}
                            onChange={(e) =>
                              handleShiftTimeChange(
                                index,
                                "from",
                                e.target.value
                              )
                            }
                            className="form-control"
                            placeholder="From Time"
                            required
                          />
                        </div>
                        <div className="col-md-3">
                          <input
                            type="time"
                            value={slot.to}
                            onChange={(e) =>
                              handleShiftTimeChange(index, "to", e.target.value)
                            }
                            className="form-control"
                            placeholder="To Time"
                            required
                          />
                        </div>
                        <div className="col-md-2">
                          <button
                            type="button"
                            className="btn btn-danger"
                            onClick={() => removeShiftTime(index)}
                            disabled={service.shiftTimes.length === 1}
                          >
                            Remove
                          </button>
                        </div>
                      </div>
                    ))}

                    <button
                      type="button"
                      className="btn btn-success mt-2"
                      onClick={addShiftTime}
                    >
                      + Add Shift Time
                    </button>
                  </div>

                  <div className="col-md-12">
                    <label className="form-label">
                      Report Time Availability
                      <span className="text-danger">*</span>
                    </label>
                    <input
                      type="text"
                      name="reportingTime"
                      value={service.reportingTime}
                      className="form-control"
                      placeholder="Write a note"
                      onChange={handleChange}
                      required
                    />
                    <small className="form-text text-muted">
                      Example: You will get your report within 2 hours after
                      checkup.
                    </small>
                  </div>

                  {/* Description */}
                  <div className="col-md-12">
                    <label className="form-label">Description</label>
                    <textarea
                      name="description"
                      value={service.description}
                      onChange={handleChange}
                      className="form-control"
                      placeholder="Enter description about service"
                      rows="3"
                      required
                    ></textarea>
                  </div>
                </div>

                <div className="mt-4">
                  <button
                    type="submit"
                    className="btn btn-main"
                    style={{
                      backgroundColor: "green",
                      color: "whitesmoke",
                      padding: "10px 20px",
                      borderRadius: "5px",
                      fontWeight: "600",
                    }}
                    disabled={loading}
                  >
                    {loading ? "Updating..." : "Update Service"}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
        {confirmModal && (
          <div
            className="modal-backdrop"
            style={{
              position: "fixed",
              top: 0,
              left: 0,
              width: "100%",
              height: "100%",
              backgroundColor: "rgba(0,0,0,0.5)",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              zIndex: 9999,
            }}
          >
            <div
              className="modal-content"
              style={{
                backgroundColor: "white",
                padding: "20px",
                borderRadius: "8px",
                width: "600px",
                textAlign: "center",
              }}
            >
              <h5>Please verify the details you filled</h5>
              <p>Do you want to update the service?</p>
              <div
                className="mt-3"
                style={{
                  display: "flex",
                  justifyContent: "center",
                  gap: "10px",
                }}
              >
                <button
                  className="btn btn-success"
                  onClick={confirmSaveService}
                >
                  Yes
                </button>
                <button
                  className="btn btn-danger"
                  onClick={() => setConfirmModal(false)}
                >
                  No
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
      <style>
        {`@media (max-width: 576px) {
  .shift-row {
    gap: 10px; /* vertical spacing between inputs */
  }
}
    @media (max-width: 625px) {
    /* Make the card full width with some padding */
    .modal-backdrop {
     
      margin: 12px auto !important;
      padding: 15px !important;
    }

    .shift-row {
      flex-direction: column !important;
      gap: 10px;
    }

    .col-md-4,
    .col-md-6,
    .col-md-12 {
      flex: 0 0 100% !important;
      max-width: 100% !important;
    }

    .btn {
     
      margin-top: 5px !important;
    }

    /* Optional: reduce font size on small screens */
    .card-body {
      font-size: 14px;
    }
  }
          `}
      </style>
      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default EditServiceLayer;
